// Handle TypeScript declarations
declare const marked: { parse: (markdown: string) => string };
declare const DOMPurify: { sanitize: (html: string) => string };
declare const mermaid: {
  initialize: (config: { startOnLoad: boolean; theme: string; }) => void;
  run: () => void;
};
declare const hljs: {
  highlightElement: (element: Element) => void;
};

// --- Constants ---
const FILE_INDEX_KEY = 'markdown_files_index';
const FILE_CONTENT_PREFIX = 'markdown_file_';
const SIDEBAR_STATE_KEY = 'sidebar_collapsed_state';
const INITIAL_MARKDOWN = `# Markdown Previewer with Local Storage

This is a real-time markdown editor. Your files are saved directly in your browser.

## Features

- **Live Preview:** Your markdown is rendered as you type.
- **Mermaid Support:** Create diagrams easily.
- **PDF Export:** Save your work as a PDF.
- **Themeable:** Switch between light and dark themes.
- **Local Storage:** Your files are saved in your browser, no login required!

### Example Diagram
Correct rendering of HTML tags like <br/> is now supported:
\`\`\`mermaid
graph TB
    subgraph "UI Layer"
        Pages["Pages/Components<br/>(React)"]
    end
    
    subgraph "Presenter Layer"
        Controller["Controllers<br/>(UI Event Handling)"]
        Transfer["Transfer<br/>(Entity → ViewModel)"]
        ViewModel["ViewModels<br/>(UI State)"]
    end
    
    Pages --> Controller
    Controller --> Transfer
    Transfer --> ViewModel
    Pages --> ViewModel
\`\`\`
`;

/**
 * Main application class to encapsulate all functionality.
 */
class MarkdownEditorApp {
    private state: {
        currentFile: { id: string, name: string } | null;
    };

    private dom: {
        [key: string]: HTMLElement;
    };

    constructor() {
        this.state = {
            currentFile: null,
        };
        
        // A central place for all DOM element references
        this.dom = {
            editor: document.getElementById('editor') as HTMLTextAreaElement,
            preview: document.getElementById('preview') as HTMLDivElement,
            styleSelector: document.getElementById('style-selector') as HTMLSelectElement,
            savePdfButton: document.getElementById('save-pdf') as HTMLButtonElement,
            saveButton: document.getElementById('save-button') as HTMLButtonElement,
            newDocButton: document.getElementById('new-doc-button') as HTMLButtonElement,
            fileList: document.getElementById('file-list') as HTMLUListElement,
            helpButton: document.getElementById('help-button') as HTMLButtonElement,
            helpModal: document.getElementById('help-modal') as HTMLDivElement,
            closeModalButton: document.getElementById('close-modal-button') as HTMLButtonElement,
            sidebar: document.getElementById('file-sidebar') as HTMLElement,
            sidebarToggle: document.getElementById('sidebar-toggle') as HTMLButtonElement,
            resizer: document.getElementById('resizer') as HTMLDivElement,
            editorSection: document.getElementById('editor-section') as HTMLElement,
            mainContent: document.getElementById('main-content') as HTMLElement,
            fileInfo: document.getElementById('file-info')!,
            currentFileName: document.getElementById('current-file-name')!,
        };
        
        this.init();
    }
    
    /**
     * Initializes the application, binds events, and loads initial state.
     */
    init() {
        if (!this.checkLocalStorageSupport()) {
            alert('경고: 이 브라우저는 로컬 저장소를 지원하지 않습니다. 파일 저장 기능이 제한될 수 있습니다.');
        }
        
        mermaid.initialize({ startOnLoad: false, theme: 'default' });
        this.bindEvents();

        const savedSidebarState = this.storageGet(SIDEBAR_STATE_KEY) === 'true';
        this.setSidebarState(savedSidebarState);

        (this.dom.editor as HTMLTextAreaElement).value = INITIAL_MARKDOWN;
        this.updatePreview();
        this.renderFileList();
    }
    
    private checkLocalStorageSupport(): boolean {
        try {
            const testKey = 'localStorage_test';
            localStorage.setItem(testKey, 'test');
            localStorage.removeItem(testKey);
            return true;
        } catch (error) {
            return false;
        }
    }

    /**
     * Binds all application event listeners.
     */
    bindEvents() {
        this.dom.editor.addEventListener('input', () => this.updatePreview());
        this.dom.styleSelector.addEventListener('change', () => this.handleThemeChange());
        this.dom.savePdfButton.addEventListener('click', () => this.handleSavePdf());
        this.dom.saveButton.addEventListener('click', () => this.saveFile());
        this.dom.newDocButton.addEventListener('click', () => this.createNewFile());
        this.dom.fileList.addEventListener('click', (e) => this.handleFileListClick(e));
        
        // Modal events
        this.dom.helpButton.addEventListener('click', () => this.toggleHelpModal(true));
        this.dom.closeModalButton.addEventListener('click', () => this.toggleHelpModal(false));
        this.dom.helpModal.addEventListener('click', (e) => {
            if (e.target === this.dom.helpModal) {
                this.toggleHelpModal(false);
            }
        });

        // Sidebar and resizer events
        this.dom.sidebarToggle.addEventListener('click', () => this.handleSidebarToggle());
        this.dom.resizer.addEventListener('mousedown', (e) => this.initResizer(e as MouseEvent));
    }

    // --- UI Update Methods ---

    updatePreview() {
        const markdownText = (this.dom.editor as HTMLTextAreaElement).value;
        const dirtyHtml = marked.parse(markdownText);
        const cleanHtml = DOMPurify.sanitize(dirtyHtml);
        this.dom.preview.innerHTML = cleanHtml;

        const mermaidCodeBlocks = this.dom.preview.querySelectorAll('code.language-mermaid');
        mermaidCodeBlocks.forEach(codeBlock => {
            const preElement = codeBlock.parentElement;
            if (preElement) {
                const mermaidContainer = document.createElement('div');
                mermaidContainer.classList.add('mermaid');
                const decoder = document.createElement('textarea');
                decoder.innerHTML = codeBlock.innerHTML;
                mermaidContainer.textContent = decoder.value;
                preElement.replaceWith(mermaidContainer);
            }
        });

        // Apply syntax highlighting to all code blocks (except mermaid)
        this.dom.preview.querySelectorAll('pre code:not(.language-mermaid)').forEach((block) => {
            hljs.highlightElement(block);
        });

        try {
            mermaid.run();
        } catch (e) {
            console.error('Error rendering Mermaid diagram:', e);
        }
    }
    
    renderFileList() {
        this.dom.fileList.innerHTML = '';
        const index = this.storageGetFileIndex();
        if (index.length === 0) {
            this.dom.fileList.innerHTML = '<li>No files found.</li>';
            return;
        }
        index.sort((a, b) => a.name.localeCompare(b.name));
        index.forEach(file => {
            const li = document.createElement('li');
            li.dataset.id = file.id;
            li.dataset.name = file.name;
            li.setAttribute('role', 'button');
            li.tabIndex = 0;
            
            li.innerHTML = `
                <span class="file-name">${file.name}</span>
                <button class="delete-btn" data-id="${file.id}" data-name="${file.name}" aria-label="Delete ${file.name}">&times;</button>
            `;
            this.dom.fileList.appendChild(li);
        });
    }

    updateFileInfo() {
        if (this.state.currentFile) {
            this.dom.fileInfo.style.display = 'block';
            this.dom.currentFileName.textContent = this.state.currentFile.name;
        } else {
            this.dom.fileInfo.style.display = 'none';
        }
    }

    setSidebarState(isCollapsed: boolean) {
        this.dom.sidebar.classList.toggle('collapsed', isCollapsed);
        this.dom.sidebarToggle.classList.toggle('collapsed', isCollapsed);
        this.dom.sidebarToggle.setAttribute('aria-expanded', String(!isCollapsed));
        this.dom.sidebarToggle.innerHTML = isCollapsed ? '›' : '‹';
        this.storageSet(SIDEBAR_STATE_KEY, String(isCollapsed));
    }

    toggleHelpModal(show: boolean) {
        this.dom.helpModal.style.display = show ? 'flex' : 'none';
    }

    // --- Event Handlers ---

    handleThemeChange() {
        const selectedTheme = (this.dom.styleSelector as HTMLSelectElement).value;
        this.dom.preview.className = `preview-style ${selectedTheme}`;
        this.updatePreview();
    }

    handleSavePdf() {
        try {
            window.print();
        } catch (error) {
            console.error("Failed to open print dialog:", error);
            alert("Could not open the print dialog. Please check browser settings.");
        }
    }

    handleFileListClick(e: Event) {
        const target = e.target as HTMLElement;
        // Fix: Cast element to HTMLElement to allow access to dataset.
        const deleteButton = target.closest('.delete-btn') as HTMLElement | null;
        const listItem = target.closest('li') as HTMLElement | null;

        if (deleteButton && deleteButton.dataset.id) {
            this.deleteFile(deleteButton.dataset.id, deleteButton.dataset.name!);
        } else if (listItem && listItem.dataset.id) {
            this.loadFile(listItem.dataset.id);
        }
    }
    
    handleSidebarToggle() {
        const isCollapsed = this.dom.sidebar.classList.contains('collapsed');
        this.setSidebarState(!isCollapsed);
    }
    
    // --- Core Logic Methods ---

    createNewFile() {
        this.state.currentFile = null;
        (this.dom.editor as HTMLTextAreaElement).value = "# New Document\n\nStart writing!";
        this.updatePreview();
        this.updateFileInfo();
    }

    loadFile(id: string) {
        const index = this.storageGetFileIndex();
        const fileMeta = index.find(f => f.id === id);
        if (!fileMeta) {
            alert("File not found!");
            return;
        }
        const content = this.storageGet(`${FILE_CONTENT_PREFIX}${id}`);
        if (content !== null) {
            (this.dom.editor as HTMLTextAreaElement).value = content;
            this.state.currentFile = fileMeta;
            this.updatePreview();
            this.updateFileInfo();
        } else {
            alert("Could not load file content.");
        }
    }

    saveFile() {
        const content = (this.dom.editor as HTMLTextAreaElement).value;
        let index = this.storageGetFileIndex();

        if (this.state.currentFile) {
            // Scenario: Saving an already opened/saved file
            this.storageSet(`${FILE_CONTENT_PREFIX}${this.state.currentFile.id}`, content);
            alert(`File "${this.state.currentFile.name}" updated.`);
        } else {
            // Scenario: Saving a new file ("Save As")
            const promptedName = prompt("Enter filename (e.g., my-document.md):", "untitled.md");
            if (!promptedName || !promptedName.trim()) return; // User cancelled

            const fileName = promptedName.trim();
            const existingFile = index.find(f => f.name.toLowerCase() === fileName.toLowerCase());

            if (existingFile) {
                // Sub-scenario: Overwriting an existing file
                if (!confirm(`A file named "${fileName}" already exists. Do you want to overwrite it?`)) return;
                
                this.storageSet(`${FILE_CONTENT_PREFIX}${existingFile.id}`, content);
                this.state.currentFile = existingFile;
                alert(`File "${existingFile.name}" overwritten.`);
            } else {
                // Sub-scenario: Saving a completely new file
                const newFile = { id: Date.now().toString(), name: fileName };
                index.push(newFile);
                this.storageSet(`${FILE_CONTENT_PREFIX}${newFile.id}`, content);
                this.storageSetFileIndex(index);
                this.state.currentFile = newFile;
                alert(`File "${newFile.name}" saved.`);
            }
            this.renderFileList();
            this.updateFileInfo();
        }
    }
    
    deleteFile(id: string, name: string) {
        if (!confirm(`Are you sure you want to delete "${name}"? This cannot be undone.`)) return;
        
        let index = this.storageGetFileIndex();
        index = index.filter(f => f.id !== id);
        
        try {
            localStorage.removeItem(`${FILE_CONTENT_PREFIX}${id}`);
        } catch (error) {
            console.error('Error removing file from localStorage:', error);
        }
        
        this.storageSetFileIndex(index);

        if (this.state.currentFile && this.state.currentFile.id === id) {
           this.createNewFile();
        }
        this.renderFileList();
    }
    
    initResizer(e: MouseEvent) {
        e.preventDefault();
        document.body.style.userSelect = 'none';
        document.body.style.cursor = 'col-resize';

        const handleMouseMove = (moveEvent: MouseEvent) => {
            const sidebarWidth = this.dom.sidebar.classList.contains('collapsed') ? 0 : this.dom.sidebar.offsetWidth;
            const toggleWidth = this.dom.sidebarToggle.offsetWidth;
            const containerRect = this.dom.mainContent.getBoundingClientRect();
            const newEditorWidth = moveEvent.clientX - containerRect.left - sidebarWidth - toggleWidth;
            const minWidth = 150;
            const containerWidth = containerRect.width;

            if (newEditorWidth > minWidth && (containerWidth - newEditorWidth - this.dom.resizer.offsetWidth) > minWidth) {
                this.dom.editorSection.style.flexGrow = '0';
                this.dom.editorSection.style.flexBasis = `${newEditorWidth}px`;
            }
        };

        const handleMouseUp = () => {
            document.removeEventListener('mousemove', handleMouseMove);
            document.removeEventListener('mouseup', handleMouseUp);
            document.body.style.userSelect = '';
            document.body.style.cursor = '';
        };

        document.addEventListener('mousemove', handleMouseMove);
        document.addEventListener('mouseup', handleMouseUp);
    }

    // --- Local Storage Helpers ---

    private storageGet(key: string): string | null {
        try {
            return localStorage.getItem(key);
        } catch (error) {
            console.error('Error reading from localStorage:', error);
            return null;
        }
    }

    private storageSet(key: string, value: string) {
        try {
            localStorage.setItem(key, value);
        } catch (error) {
            console.error('Error writing to localStorage:', error);
            alert('저장 실패: 브라우저 저장소에 접근할 수 없습니다. 개인정보 보호 모드이거나 저장소가 가득 찬 것일 수 있습니다.');
        }
    }
    
    private storageGetFileIndex(): Array<{id: string, name: string}> {
        const indexJson = this.storageGet(FILE_INDEX_KEY);
        if (!indexJson) return [];
        
        try {
            const parsed = JSON.parse(indexJson);
            return Array.isArray(parsed) ? parsed : [];
        } catch (error) {
            console.error('Error parsing file index from localStorage:', error);
            this.storageSet(FILE_INDEX_KEY, JSON.stringify([]));
            return [];
        }
    }
    
    private storageSetFileIndex(index: Array<{id: string, name: string}>) {
        try {
            this.storageSet(FILE_INDEX_KEY, JSON.stringify(index));
        } catch (error) {
            console.error('Error saving file index to localStorage:', error);
            alert('파일 목록 저장에 실패했습니다.');
        }
    }
}

document.addEventListener('DOMContentLoaded', () => {
    new MarkdownEditorApp();
});
